
'use client';

import * as THREE from 'three';
import { Canvas, extend, useFrame } from '@react-three/fiber';
import { useMemo, useRef } from 'react';
import { shaderMaterial } from '@react-three/drei';

const NoiseMaterial = shaderMaterial(
  {
    uTime: 0,
    uFrequency: new THREE.Vector2(10, 5),
    uColor: new THREE.Color('hsl(233, 30%, 12%)'),
    uDarkColor: new THREE.Color('hsl(182, 100%, 74%)'),
    uLightColor: new THREE.Color('hsl(233, 54%, 37%)'),
  },
  `
    varying vec2 vUv;
    void main() {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  `
    varying vec2 vUv;
    uniform float uTime;
    uniform vec3 uColor;
    uniform vec3 uDarkColor;
    uniform vec3 uLightColor;

    // Simplex 2D noise
    vec3 permute(vec3 x) { return mod(((x*34.0)+1.0)*x, 289.0); }

    float snoise(vec2 v){
      const vec4 C = vec4(0.211324865405187, 0.366025403784439, -0.577350269189626, 0.024390243902439);
      vec2 i  = floor(v + dot(v, C.yy) );
      vec2 x0 = v -   i + dot(i, C.xx);
      vec2 i1;
      i1 = (x0.x > x0.y) ? vec2(1.0, 0.0) : vec2(0.0, 1.0);
      vec4 x12 = x0.xyxy + C.xxzz;
      x12.xy -= i1;
      i = mod(i, 289.0);
      vec3 p = permute( permute( i.y + vec3(0.0, i1.y, 1.0 )) + i.x + vec3(0.0, i1.x, 1.0 ));
      vec3 m = max(0.5 - vec3(dot(x0,x0), dot(x12.xy,x12.xy), dot(x12.zw,x12.zw)), 0.0);
      m = m*m ;
      m = m*m ;
      vec3 x = 2.0 * fract(p * C.www) - 1.0;
      vec3 h = abs(x) - 0.5;
      vec3 ox = floor(x + 0.5);
      vec3 a0 = x - ox;
      m *= 1.79284291400159 - 0.85373472095314 * ( a0*a0 + h*h );
      vec3 g;
      g.x  = a0.x  * x0.x  + h.x  * x0.y;
      g.yz = a0.yz * x12.xz + h.yz * x12.yw;
      return 130.0 * dot(m, g);
    }


    void main() {
      float noise = snoise(vUv * 2.0 - uTime * 0.05);

      // Add a moving band of light
      float band = smoothstep(0.4, 0.6, vUv.y);
      noise *= band;

      // Color mapping
      vec3 finalColor = mix(uDarkColor, uLightColor, smoothstep(-1.0, 1.0, noise));

      gl_FragColor = vec4(finalColor, 1.0);
    }
  `
);

extend({ NoiseMaterial });

const Plane = () => {
  const materialRef = useRef<any>();
  useFrame(({ clock }) => {
    if (materialRef.current) {
      materialRef.current.uTime = clock.getElapsedTime();
    }
  });

  return (
    <mesh>
      <planeGeometry args={[10, 10, 32, 32]} />
      {/* @ts-ignore */}
      <noiseMaterial ref={materialRef} />
    </mesh>
  );
};

export default function LiquidEtherBackground() {
  return (
    <div className="fixed top-0 left-0 w-full h-full -z-20">
      <Canvas camera={{ position: [0, 0, 1.5] }}>
        <Plane />
      </Canvas>
    </div>
  );
}
